<template>
  <div class="face" :style="{padding:0}">
    <div class="part-one" :style="{'padding-top': $store.state.selectTab.length > 1 ? '0.6em' : '0em'}">
      
      <div class="face-content">
        <div class="detailInfos">
          <div class="face-header faceHeader">
            <img src="dist/img/face/sh.png">
            <span>商户信息</span>
          </div>
          <div class="content" >
            <!--<p class="hasColor" v-html="userName"></p>-->
             <div class="numberContent">
              <div class="circleDiv color1">
                <div class="circle " @click="routerPush('file', 'user')">
                <span class="bigNumber" :title="totalAccount['cstTotal']">
                     {{getBigNum(totalAccount['cstTotal'])}}
                     <span class="spanBorder"></span>
                </span>
                </div>
                <p class="face-header"><img src="dist/img/face/facezs.png" style="margin-top:0.25em;width:1.28em;height:1.31em"><span>总数</span></p>
              </div>
               <div class="circleDiv color2">
                 <div class="circle" @click="getAlermDetail">
                  <span class="bigNumber" :title="$store.state.alarmNum">
                  <!--   {{$store.state.alarmNum}} -->
                     {{getBigNum($store.state.alarmNum)}}
                    <span class="spanBorder"></span>
                  </span>
                 </div>
                 <p class="face-header"><img src="dist/img/face/facebj.png" style="margin-top:0.25em;width:1.28em;height:1.31em"><span>告警</span></p>
               </div>
               <div class="circleDiv color3">
                 <div class="circle" @click="routerPush('costControl', 'onOff')">
                  <span class="bigNumber" :title="totalAccount['offCount']">
                    <!--  {{totalAccount['offCount']}} -->
                      {{getBigNum(totalAccount['offCount'])}}
                     <span class="spanBorder"></span>
                  </span>
                 </div>
                 <p class="face-header"><img src="dist/img/face/facelz.png" style="margin-top:0.25em;width:1.28em;height:1.31em"><span>拉闸</span></p>
               </div>
               <div class="circleDiv color4">
                 <div class="circle" @click="routerPush('accountAnalyse', 'ExceptionAccount')">
                  <span class="bigNumber" :title="totalAccount['cstErr']">
                   {{getBigNum(totalAccount['cstErr'])}}
                  <!-- {{totalAccount['cstErr']}} -->
                  </span>
                 </div>
                 <p class="face-header">
                   <img src="dist/img/face/faceyc.png" style="margin-top:0.25em;width:1.28em;height:1.31em">
                   <span>异常</span>
                 </p>
               </div>
            </div>
            <p class="face-header locationFooter">
              <span><img src="dist/img/face/location.png"  class="little-img"> &nbsp;{{$store.state.systemConfig.payAddr}}</span>
              <span style="margin-left:0"><img class="little-img" src="dist/img/face/building.png"> &nbsp;{{$store.state.systemConfig.companyName}}</span>
            </p>
          </div>
        </div>
        <div class="detailInfos">
          <div class="face-header">
            <img src="dist/img/face/classify.png">
            <span>分类用能情况</span>
          </div>
          <div class="content" >
            <div class="numberContent">
              <table class="faceTable">
                <tr v-for="(item, $index) in tableData" v-if="showTable">
                  <td width="20%"><img :src="item.img"></td>
                  <td width="40%">{{item.sumPR}} ( {{ item.unit }} )</td>
                  <td width="40%" style="color:#FA8A4A;font-weight: 600"> ￥{{item.sumRemainAmount}}</td>
                </tr>
              </table>
            </div>
            <p class="face-header locationFooter">
              <span><img  class="little-img" src="dist/img/face/date.png"> &nbsp;{{accountTime.startTime}} - {{accountTime.endTime}}</span>
            </p>
          </div>
        </div>
        <div class="detailInfos">
          <div class="face-header">
            <img src="dist/img/face/charge-balance-icon.png">
            <span>账户余额比重</span>
          </div>
          <div class="content" id="pieCanva" ref="chart" style="border-right:0">
            <div  v-show="lostPieData"class="spacialP emptyShowPic">
              <div class="emptyContent">
                <img src="dist/img/empty-data.png">
                <p>查不到相关数据</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="part-one part-two" :style="{height: $store.state.selectTab.length > 1 ? 'calc(100% - 1.4em - 41%)' : 'calc(100% - 1em - 41%)'}">
    <!--   <div class="face-header" style="margin-left: 0.5em;">
        <img src="dist/img/face/home-charts.png">
        <span>用能统计</span>
      </div> -->
      <div class="face-content">
        <div class="canvasInfo">
          <div class="content" style="position:relative">
            <div class="lineCanvas" id="lineCanva" ref="chart">
              <div class="spacialP emptyShowPic" v-show="lostLineData">
                <div class="emptyContent">
                  <img src="dist/img/empty-data.png">
                  <p>查不到相关数据</p>
                </div>
              </div>
            </div>
            <div class="labelShow">
              <div class="spanLabel">
                <div class="topSpan">
                  <span>当日用{{tabOpt[tabIndex].typeName}}</span>
                  <span class="numberShow" style="color:#FD6854">
                        {{compare['currentDay']}}
                  </span>
                  <span>{{tabOpt[tabIndex].unit}}</span>
                </div>
                <div >
                  <span>当月用{{tabOpt[tabIndex].typeName}}</span>
                  <span class="numberShow" style="color:#FD6854">
                        {{compare['currentMonth']}}
                  </span>
                  <span>{{tabOpt[tabIndex].unit}}</span>
                </div>
                <div >
                  <span>上个月用{{tabOpt[tabIndex].typeName}}</span>
                  <span class="numberShow" style="color:#3894E7">
                    {{compare['lastMonth']}}
                  </span>
                  <span>{{tabOpt[tabIndex].unit}}</span>
                </div>
           </div>
            </div>
            <div class="tabs-show tab-div"  v-show="!lostLineData">
              <ul>
                <li v-for="(tabs, $index) in tabOpt" :class="{'selectedLi':  $index == tabIndex}" style="margin-top:-1px; padding:0.136em 1.1em"
                    @click="getBtn(tabs, $index)">
                  <img :src="tabs.img">
                  <span>{{tabs.typeName}} </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>
<script>

//  import Echarts from 'echarts'
  export default {
    data () {
      return {
          isPadding: true,
          tabIndex: 0,
          tabOpt: [
            {img: 'dist/img/face/elec.png', deviceType: '07', typeName: '电', unit: 'KWH'},
            {img: 'dist/img/face/water.png', deviceType: '08', typeName: '水', unit: 'T'},
            {img: 'dist/img/face/gas.png', deviceType: '09', typeName: '气', unit: 'Nm3'}
          ],
          accountTime: {startTime: '', endTime: ''},
          compare: {currentMonth: '', lastMonth: '', percent: '', currentDay: ''},
          totalAccount: {'cstTotal': 0, 'cstErr': 0, 'offCount': 0, 'meterErrCount': 0},
          alertNum: 0,
          showTable: false,
          showCompare: false,
          tableData: {//千瓦时
              '07': {text: '电', unit: '度', img: 'dist/img/face/elecT.png'},
              '08': {text: '水', unit: '吨', img: 'dist/img/face/waterT.png'},
              '09': {text: '气', unit: '立方', img: 'dist/img/face/gasT.png'}
          },
          lostTableData: false,
          lostPieData: false,
          lostLineData: false,
          Owner: '博高信息技术股份有限公司',
          controls: [],
          pie: '',
          line: '',
          pieData: [
              {value:'', name:'≤50'},
              {value:'', name:'50～200'},
              {value:'', name:'200～500'},
              {value:'', name:'≥500'}
          ],
          lineData: {
          '07': {
              'lastYear': [8, 6, 54, 23, 23, 30, 13, 22, 15, 50, 8, 53],
              'thisYear': [4, 10, 30, 20, 23, 34, 23, 12, 5, 60, 78, 23]
              },
          '08':{
              'lastYear': [8, 6, 54, 23, 23, 30, 13, 22, 15, 50, 8, 53],
              'thisYear': [4, 10, 30, 20, 23, 34, 23, 12, 5, 60, 78, 23]
              },
          '09': {
              'lastYear': [8, 6, 54, 23, 23, 30, 13, 22, 15, 50, 8, 53],
              'thisYear': [4, 10, 30, 20, 23, 34, 23, 12, 5, 60, 78, 23]
          }
          }
      }
    },
   
    computed: {
        userName: function () {
            return localStorage.userInfo ? JSON.parse(localStorage.userInfo).userName : ''
        },
        getName: function () {
            let sysSet = this.$store.state.systemConfig || JSON.parse(localStorage.systemConfig);
             return {
                 company: sysSet.companyName,
                 address:  sysSet.payAddr
             }
        }
    },
    methods: {
        getBigNum: function (val) {
           let num = parseInt(val);
           return num > 10000 ? '9999+' : val
        },
        getAlermDetail: function () {
          let parent = this.$parent;
          parent.sendMessage();
        },
        routerPush: function (parentIcon, childIcon) {
          let parent = this.$parent;
          let menu = parent.allMenu;
          let len = menu.length;
          for(let i=0; i<len; i++) {
            let currentP = menu[i];
            if (currentP.menuIcon == parentIcon) {
              if(currentP.children && currentP.children.length > 0) {
                let cLen = currentP.children.length;
                for(let j=0; j<cLen; j++) {
                  let currentC = currentP.children[j]
                  if(currentC.menuIcon == childIcon) {
                    parent.pushMenu(currentP.menuId, currentC, i, 0);
                    break;
                  }
                }
              }
            }
          }
        },
        getNumber: function () {
            let that = this;
            that.getRequest({
                url: 'first/pageStaticCount',
                method: 'post',
                success: function (data) {
                  for (let item in data.data) {
                    that.totalAccount[item] = data.data[item]
                  }
                }
            });
        },
        getTableData: function () {
            let that = this;
            that.getRequest({
                url: 'first/pageSumDataGet',
                method: 'post',
                success: function (data) {
                  if (data.data !== null) {
                    let Data = data.data.list;
                    for (let i=0; i< Data.length; i++) {
                      for(let item in Data[i]) {
                        that.tableData[Data[i].deviceType][item] = Data[i][item]
                      }

                    }
                    that.accountTime.startTime =  data.data.startTime;
                    that.accountTime.endTime =  data.data.endTime;
                    that.showTable = true
                  } else {
                    that.lostTableData = true;
                  }
                },
                error: function () {
                   that.lostTableData = true;
               }
            })
        },
        getBtn: function (tab, index) {
            this.tabIndex = index;
            this.makeLine();
            this.getOtherData()
        },
        makePie: function () {
          let option = {
                color: [ '#A9D86E', '#5EAEE3', '#FC6655',  '#F8A20F'],
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c}块表 ({d}%)"
                },
                legend: {
                   // orient: 'vertical',
                    y: 'center',
                    top: 'bottom',
                    bottom: 10,
                    icon: 'circle',
                    data:['≤50','50～200','200～500','≥500'],
                    itemGap: 23,
                    itemWidth: 10,
                    itemHeight: 10,
                    textStyle: {
                      color: '#fff',
                      fontSize: this.getFontSize()
                    }
                },
                toolbox: {
                    show : false,
                    feature : {
                        mark : {show: true},
                        dataView : {show: true, readOnly: false},
                        magicType : {
                            show: true,
                            type: ['pie', 'funnel']
                        },
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : true,
                series :
                    {
                        name:'账户余额',
                        type: 'pie',
                        radius : ['20%', '50%'],
                        center : ['50%', '50%'],
                      //  roseType : 'radius',
                        data:this.pieData
                    }
            };
          if (this.pie != null && this.pie != "" && this.pie != undefined) {        
                  this.pie.dispose();
          }
          this.pie = echarts.init(document.getElementById('pieCanva'));
          let that = this;
          that.getRequest({
              url: 'first/pageAnalAmount',
              method: 'post',
//              url: 'dist/json/pie.json',
//              method: 'get',
              param: {deviceType: that.tabOpt[that.tabIndex].deviceType},
              success: function (data) {
                let Data = data.data;
                if (Data !== null) {
                  that.pieData[0].value = Data.c0Amount;
                  that.pieData[1].value = Data.c50Amount;
                  that.pieData[2].value = Data.c200Amount;
                  that.pieData[3].value = Data.c500Amount;
                  setTimeout(function () {
                     that.pie.setOption(option);
                  }, 10)   
                }
              },
              error: function () {
                  that.lostPieData = true
              }
          }, true);
        },
        getFontSize: function () {
          let fontSize = 16;
          let width = window.screen.width;
          let currentWidth = document.body.clientWidth;
          if(width <= 1366 || currentWidth <= 1366) {
            fontSize = 12;
          } else if(width <= 1700 || currentWidth <= 1700) {
            fontSize = 13;
          } else  {
            fontSize = 14;
          }
          return fontSize
        },
        makeLine: function () {
            let that = this;
            if (that.line != null && that.line != "" && that.line != undefined) {        
                  that.line.dispose();
             }
            that.line = echarts.init(document.getElementById('lineCanva'));
            let type = that.tabOpt[that.tabIndex].deviceType;
            that.getRequest({
                url: 'first/sumGetForMonth',
                method: 'post',
//                url: 'dist/json/faceLine.json',
//                method: 'get',
                param: {deviceType: type},
                success: function (data) {
                  let Data = data.data;
                  if (Data !== null) {
                    let lineDatas = {};
                    for (let item in Data) {
                      let yearData = [];
                      for (let i = 0; i < Data[item].dataList.length; i++) {
                        yearData.push(Data[item].dataList[i].sumPR)
                      }
                      lineDatas[item] = yearData;
                    }
                  
                    let option = {
                      color: ['#1562AA', '#B64439'],
                      legend: {
                        show : true,
                        icon: 'circle',
                        itemGap:200,
                        itemWidth: 20,
                        itemHeight: 20,
                        shadowColor: '#293043',
                        shadowOffsetX: 1,
                        shadowOffsetY: 1,
                        shadowBlur: 10,
                        padding: [5, 20, 15, 10],
                        x: 'center',
                        top: 'bottom',
                        data:['去年用能', '今年用能'],
                        textStyle : {
                          color: 'white',
                          fontSize: that.getFontSize()
                        }
                      },
                      
                       toolbox: {
                        show: false,
                        feature: {
                          dataZoom: {
                            yAxisIndex: 'none'
                          },
                          restore: {},
                          saveAsImage: {}
                        }
                      },
                      grid: {
                        left: 100,
                        right: 10,
                        bottom:80,
                        top: 80
                      },
                      xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['1月' ,'2月', '3月', '4月', '5月', '6月', '7月','8月', '9月', '10月','11月','12月'],
                        nameTextStyle: {
                          color: '#fff',
                        },
                        axisLabel: {
                          color: '#fff',
                          textStyle: {
                            color: '#fff'
                          }
                        },
                        splitLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)'
                          }
                        }
                      },
                      yAxis: {
                        type: 'value',
                        nameTextStyle: {
                          color: '#fff',
                        },
                        axisLabel: {
                          color: '#fff',
                          textStyle: {
                            color: '#fff'
                          }
                        },
                        splitLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)'
                          }
                        }
                      },
                      tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                          type: 'cross',
                          label: {
                          //  backgroundColor: '#6a7985'
                          }
                        }
                      },
                      series: [
                        {
                          name:'去年用能',
                          type:'line',
                          smooth:false,
                          symbol: 'none',
                          sampling: 'average',
                          areaStyle: {
                            normal: {
                              color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 1,
                                color: '#1562AA'
                              }, {
                                offset: 1,
                                color: '#5881B9'
                              }])
                            }
                          },
                          data: lineDatas.lastYear
                        },
                        {
                          name:'今年用能',
                          type:'line',
                          smooth:false,
                          symbol: 'none',
                          sampling: 'average',
                          itemStyle: {
                            normal: {
                              color: '#B64439'
                            }
                          },
                          areaStyle: {
                            normal: {
                              color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 1,
                                color: '#B64439'
                              }, {
                                offset: 1,
                                color: '#A86368'
                              }])
                            }
                          },
                          data: lineDatas.thisYear
                        }
                      ]
                    };

                    setTimeout(function () {

                      that.line.setOption(option);
                    }, 10)   
                  }
                },
                error: function () {
                    that.lostLineData = true
                }
            }, true);
        },
        windowChange: function () {
          this.makePie();
          this.makeLine();
          this.line.resize();
          this.pie.resize();
        },
        getOtherData: function () {
            let that = this;
            let type = that.tabOpt[that.tabIndex].deviceType;
            that.getRequest({
                url: 'first/pageSumMonthPer',
                method: 'post',
//                url: 'dist/json/faceTotals.json',
//                method: 'get',
                param: {deviceType: type},
                success: function (data) {
                  for (let item in data.data) {
                    that.compare[item] = data.data[item]
                  }
                  that.showCompare = false;
                  setTimeout(function () {
                    that.showCompare = true;
                  }, 0)
                }
            }, true);
        },
        init: function () {
            this.getNumber();
            this.getTableData();
            //this.makePie();
            this.getOtherData();
           // this.makeLine();
            this.windowChange();
            let that = this;
            that.isPadding = !(this.$parent.selectTab.length > 1);
            // window.onresize = function () {
            //   that.resizeCharts()
            // }
        }
    },
    beforeRouteEnter (to, from, next) {
          next(vm => {
            if(to.name != from.name && from.name !== null) {
               vm.init();
            }
          });
    },
    mounted: function () {
        this.isIn();
        this.getAction();
    },
    watch: {

    }
  }
</script>

