         }
       }
       .littleCanvas{
         width: 100%;
         height: 240px
       }
       p{
         i{
           margin-right: 10px;
         }
       }
     }
   }
   div.detailInfos:last-child{
     border-right:0
   }
   div.canvasInfo{
     float: left;
     width: 100%;
     height:100%;
    // height: calc(100% - 350px);
     div.content {
       height: calc(100%);
       .lineCanvas{
         height: 100%;
         width: 64%;
         float:left;
       }
       .labelShow {
         width: calc(36% - 1em);
         position:absolute;
         height:100%;
         left:65%;
         display: table;
         top: 0;
         padding-left:1em;
         text-align: left;
         .spanLabel{
           display: table-cell;
           vertical-align: middle;
           color: white;
           padding: 0.4em 0.8em;
           font-size:1em;
           div{
             text-align: center;
             display: inline-block;
             height: 7em;
            // background: rgb(23, 38, 61);
            background:#305ea0;
             padding: 0.8em 0.6em;
             min-width:35%;
             margin:  0 0.5em;
             border-radius: 0.2em;
             .spanRight{
               width: 90px;
               float: right;
               text-align: center;
               padding-top: 10px;
               .glyphicon-arrow-down,  .glyphicon-arrow-up{
                 font-size: 55px;
               }
             }
             span{
               display: block;
               width: calc(100% - 10px);
               margin: 8px 5px;
               font-size: 0.9em;
             }
             .numberShow{
               font-size: 1.8em;
               width: 100%;
               margin:0.5em 0;
               span{
                 font-size: 0.6em;
                 display: inline;
               }
             }
           }
           .topSpan{
              margin:0 23% 1em 23%
           }
         }
       }
      .tab-div{
        position: absolute;
        top: 0.5em;
        left: 1.5em;
      }
     }
   }
 }
  .face::-webkit-scrollbar {
     display:none
  }
  .little-img{
    width:1.55em!important;height:1.55em!important;margin-top:0.3em!important;
  }
</style>



